export default {
  initMenu(e) {
    var arr = itemList;
    
    if (e === "admin") {
      for (let index = 0; index < arr.length; index++) {
        if (arr[index].name === "") {
          arr[index].status = true;
        }
        if (arr[index].name === "") {

          arr[index].status = true;
        }
      }

    }else if(e === "station"){
      for (let index = 0; index < arr.length; index++) {
        if (arr[index].name === "") {
          arr[index].status = false;
        }
        if (arr[index].name === "") {

          arr[index].status = true;
        }
      }
    }
    else{
      for (let index = 0; index < arr.length; index++) {
        if (arr[index].name === "") {
          arr[index].status = false;
        }
        if (arr[index].name === "") {

          arr[index].status = false;
        }
      }
    }
    return arr;
  }
};


